body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-attachment: fixed;
  background-image : -webkit-gradient(linear,left top, right bottom,from(#E7040F),color-stop(#FF4136),to(#FF725C));
  background-image : -webkit-linear-gradient(left top,#E7040F,#FF4136,#FF725C);
  background-image : linear-gradient(to right bottom,#E7040F,#FF4136,#FF725C);
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  padding-bottom: 20px;
  background-position: fixed
}

@-webkit-keyframes example{
  0%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#E7040F),color-stop(#FF4136),to(#FF725C));
    background-image : -webkit-linear-gradient(left top,#E7040F,#FF4136,#FF725C);
    background-image : linear-gradient(to right bottom,#E7040F,#FF4136,#FF725C);
  }
  50%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#FF725C),color-stop(#E7040F),to(#FF4136));
    background-image : -webkit-linear-gradient(left top,#FF725C,#E7040F,#FF4136);
    background-image : linear-gradient(to right bottom,#FF725C,#E7040F,#FF4136);
  }
  75%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#FF4136),color-stop(#FF725C),to(#E7040F));
    background-image : -webkit-linear-gradient(left top,#FF4136,#FF725C,#E7040F);
    background-image : linear-gradient(to right bottom,#FF4136,#FF725C,#E7040F);
  }
  100%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#E7040F),color-stop(#FF4136),to(#FF725C));
    background-image : -webkit-linear-gradient(left top,#E7040F,#FF4136,#FF725C);
    background-image : linear-gradient(to right bottom,#E7040F,#FF4136,#FF725C);
  }
}

@keyframes example{
  0%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#E7040F),color-stop(#FF4136),to(#FF725C));
    background-image : -webkit-linear-gradient(left top,#E7040F,#FF4136,#FF725C);
    background-image : linear-gradient(to right bottom,#E7040F,#FF4136,#FF725C);
  }
  50%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#FF725C),color-stop(#E7040F),to(#FF4136));
    background-image : -webkit-linear-gradient(left top,#FF725C,#E7040F,#FF4136);
    background-image : linear-gradient(to right bottom,#FF725C,#E7040F,#FF4136);
  }
  75%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#FF4136),color-stop(#FF725C),to(#E7040F));
    background-image : -webkit-linear-gradient(left top,#FF4136,#FF725C,#E7040F);
    background-image : linear-gradient(to right bottom,#FF4136,#FF725C,#E7040F);
  }
  100%{
    background-image : -webkit-gradient(linear,left top, right bottom,from(#E7040F),color-stop(#FF4136),to(#FF725C));
    background-image : -webkit-linear-gradient(left top,#E7040F,#FF4136,#FF725C);
    background-image : linear-gradient(to right bottom,#E7040F,#FF4136,#FF725C);
  }
}

